<template>
  <div class="the-menu">
    <van-tabbar
    v-model="active"
    fixed
    active-color="orange">
      <van-tabbar-item
      v-for="item of MENU"
      :key="item.index"
      :to="item.path">{{ item.label }}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { MENU } from '@/utils'

export default {
  name: 'Menu',
  data () {
    return {
      MENU,
      active: 0 // 默认选中index
    }
  }
}
</script>

<style>
.the-menu {
  color: black;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 33px;
}
.the-menu .van-tabbar {
  height: 33px;
}
</style>



