// 数据请求地址
export const BASE_URL = 'https://api.apiopen.top/' // 测试使用的开放API

// 响应状态码
export const CODE = {
  SUCCESS: 200,
  NO_PERMISSION: 666,
  NOT_FOUND: 404
}
// 提示信息
export const PROMPT = {
  EMPTY_TEXT: '数据正在更新中'
}

// 获取content高度（动态）= 移动设备可见高度 - Header高度 - SubHeader高度 - Title高度 - Menu高度
export const NON_DYNAMIC_HEIGHT = document.documentElement.clientHeight - 24 - 20 - 20 - 33

// 设置Menu项(统一设置页面名称、路由path&name)
export const MENU = [{
  index: 1,
  label: '单类',
  path: 'singlePart',
  name: 'singlePart',
  // 注：因router组件需要栏加载，此处暂不支持component的变量设置
  componentUrl: '@/components/SinglePart'
}, {
  index: 2,
  label: '单类多源',
  path: 'multipleSourcePart',
  name: 'multipleSourcePart',
  componentUrl: '@/components/MultipleSourcePart'
}, {
  index: 3,
  label: '多类多样',
  path: 'MultiplePart',
  name: 'MultiplePart',
  componentUrl: '@/components/MultiplePart'
}, {
  index: 4,
  label: '扩展多样',
  path: 'expandMultiplePart',
  name: 'expandMultiplePart',
  componentUrl: '@/components/ExpandMultiplePart'
}]



