      <van-col class="text-left" span="14">界面提示信息</van-col>
      <van-col class="text-right" span="10">用户信息</van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  name: 'Header'
}
</script>

<style>
.the-header {
  position: relative;
  width: 100%;
  height: 24px;
  background-color: orange;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.the-header .text-left,
.the-header .text-right {
  line-height: 24px;
  padding: 0 12px;
}
</style>



