import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import { MENU } from '@/utils'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      meta: {
        title: '首页'
      },
      redirect: '/' + MENU[0].path,
      component: Home,
      children: [{
        path: MENU[0].path, // 路由信息与Menu数据一致
        name: MENU[0].name,
        meta: {
          title: MENU[0].label
        },
        // component: () => import(`${MENU[0].componentUrl}`)
        component: () => import('@/components/SinglePart')
      }, {
        path: MENU[1].path,
        name: MENU[1].name,
        meta: {
          title: MENU[1].label
        },
        // component: () => import(`${MENU[1].componentUrl}`)
        component: () => import('@/components/MultipleSourcePart')
      }, {
        path: MENU[2].path,
        name: MENU[2].name,
        meta: {
          title: MENU[2].label
        },
        // component: () => import(`${MENU[2].componentUrl}`)
        component: () => import('@/components/MultiplePart')
      }, {
        path: MENU[3].path,
        name: MENU[3].name,
        meta: {
          title: MENU[3].label
        },
        // component: () => import(`${MENU[3].componentUrl}`)
        component: () => import('@/components/ExpandMultiplePart')
      }]
    }, {
      path: '/prompt',
      name: 'prompt',
      meta: {
        title: '信息提示'
      },
      component: () => import('@/views/Prompt')
    }, {
      path: '/notFound',
      name: 'notFoundng',
      meta: {
        title: '资源不存在'
      },
      component: () => import('@/views/NotFound')
    }
  ]
})



